'use client';

import React, { useState, useEffect } from 'react';
import { useVoting } from '@/context/ContractContext';
import ElectionCard from '@/components/ElectionCard';
import CandidateList from '@/components/CandidateList';
import VoterStatusCard from '@/components/VoterStatusCard';
import { ArrowLeft, Vote, Clock, Users } from 'lucide-react';

const UserPortal = () => {
  const { isConnected, account, isLoading } = useVoting();
  const [selectedElection, setSelectedElection] = useState(null);
  const [activeElections, setActiveElections] = useState([]);
  const [voterStatus, setVoterStatus] = useState(null);

  // Mock data for demonstration (replace with actual contract calls later)
  const mockElections = [
    {
      electionId: '1',
      title: 'Presidential Election 2024',
      description: 'Election for the President of the Republic',
      startTime: '1704067200', // Jan 1, 2024
      endTime: '1735689600', // Dec 31, 2024
      isActive: true,
      totalVotes: '1250'
    },
    {
      electionId: '2',
      title: 'Provincial Assembly Election',
      description: 'Election for Provincial Assembly Members',
      startTime: '1704067200',
      endTime: '1735689600',
      isActive: true,
      totalVotes: '890'
    },
    {
      electionId: '3',
      title: 'Local Government Election',
      description: 'Election for Local Government Representatives',
      startTime: '1704067200',
      endTime: '1735689600',
      isActive: false,
      totalVotes: '0'
    }
  ];

  const mockVoterStatus = {
    isRegistered: true,
    hasVoted: false,
    votedCandidateId: '0'
  };

  useEffect(() => {
    // Filter active elections
    const active = mockElections.filter(election => election.isActive);
    setActiveElections(active);
    setVoterStatus(mockVoterStatus);
  }, []);

  const handleElectionSelect = (election) => {
    setSelectedElection(election);
  };

  const handleBackToElections = () => {
    setSelectedElection(null);
  };

  if (!isConnected) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center">
        <div className="bg-white rounded-lg shadow-lg p-8 max-w-md w-full mx-4">
          <div className="text-center">
            <Vote className="w-16 h-16 text-blue-600 mx-auto mb-4" />
            <h2 className="text-2xl font-bold text-gray-900 mb-2">
              Connect Your Wallet
            </h2>
            <p className="text-gray-600 mb-6">
              Please connect your wallet to access the voting portal and participate in elections.
            </p>
            <button className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-6 rounded-lg transition-colors">
              Connect Wallet
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-3">
                <Vote className="w-8 h-8 text-blue-600" />
                Voter Portal
              </h1>
              <p className="text-gray-600 mt-1">
                Connected: {account?.slice(0, 6)}...{account?.slice(-4)}
              </p>
            </div>
            <div className="flex items-center gap-4">
              <div className="text-right">
                <p className="text-sm text-gray-500">Active Elections</p>
                <p className="text-2xl font-bold text-blue-600">{activeElections.length}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Main Content */}
        {!selectedElection ? (
          <div className="space-y-8">
            {/* Voter Status */}
            {voterStatus && (
              <VoterStatusCard 
                voterStatus={voterStatus}
                electionId={activeElections[0]?.electionId}
              />
            )}

            {/* Active Elections */}
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-6 flex items-center gap-2">
                <Clock className="w-6 h-6 text-green-600" />
                Active Elections
              </h2>
              
              {activeElections.length === 0 ? (
                <div className="bg-white rounded-lg shadow-sm p-12 text-center">
                  <Vote className="w-16 h-16 text-gray-400 mx-auto mb-4" />
                  <h3 className="text-xl font-semibold text-gray-600 mb-2">
                    No Active Elections
                  </h3>
                  <p className="text-gray-500">
                    There are currently no active elections. Check back later.
                  </p>
                </div>
              ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                  {activeElections.map((election) => (
                    <ElectionCard
                      key={election.electionId}
                      election={election}
                      onSelect={handleElectionSelect}
                      voterStatus={voterStatus}
                    />
                  ))}
                </div>
              )}
            </div>

            {/* All Elections */}
            <div>
              <h2 className="text-2xl font-bold text-gray-900 mb-6 flex items-center gap-2">
                <Users className="w-6 h-6 text-gray-600" />
                All Elections
              </h2>
              
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {mockElections.map((election) => (
                  <ElectionCard
                    key={election.electionId}
                    election={election}
                    onSelect={handleElectionSelect}
                    voterStatus={voterStatus}
                    showInactive={true}
                  />
                ))}
              </div>
            </div>
          </div>
        ) : (
          /* Selected Election View */
          <div>
            <div className="mb-6">
              <button
                onClick={handleBackToElections}
                className="flex items-center gap-2 text-blue-600 hover:text-blue-700 font-medium mb-4 transition-colors"
              >
                <ArrowLeft className="w-4 h-4" />
                Back to Elections
              </button>
              
              <div className="bg-white rounded-lg shadow-sm p-6">
                <h2 className="text-2xl font-bold text-gray-900 mb-2">
                  {selectedElection.title}
                </h2>
                <p className="text-gray-600 mb-4">
                  {selectedElection.description}
                </p>
                
                <div className="flex items-center gap-6 text-sm text-gray-500">
                  <div className="flex items-center gap-2">
                    <Clock className="w-4 h-4" />
                    <span>
                      Ends: {new Date(parseInt(selectedElection.endTime) * 1000).toLocaleDateString()}
                    </span>
                  </div>
                  <div className="flex items-center gap-2">
                    <Users className="w-4 h-4" />
                    <span>{selectedElection.totalVotes} votes cast</span>
                  </div>
                  <div className={`px-2 py-1 rounded-full text-xs font-medium ${
                    selectedElection.isActive 
                      ? 'bg-green-100 text-green-800' 
                      : 'bg-gray-100 text-gray-800'
                  }`}>
                    {selectedElection.isActive ? 'Active' : 'Inactive'}
                  </div>
                </div>
              </div>
            </div>

            <CandidateList 
              electionId={selectedElection.electionId}
              voterStatus={voterStatus}
              isElectionActive={selectedElection.isActive}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default UserPortal;