'use client';

import React, { useState, useEffect } from 'react';
import { useVoting } from '@/context/ContractContext';
import CandidateCard from '@/components/CandidateCard';
import { Users, Trophy, BarChart3, Loader2 } from 'lucide-react';

const CandidateList = ({ electionId, isElectionActive }) => {
  const {
    getElectionCandidates,
    getCandidateDetails,
    castVote,
    isLoading,
    account
  } = useVoting();

  const [candidates, setCandidates] = useState([]);
  const [selectedCandidate, setSelectedCandidate] = useState(null);
  const [showResults, setShowResults] = useState(false);
  const [loadingCandidates, setLoadingCandidates] = useState(false);
  const [votingInProgress, setVotingInProgress] = useState(false);

  // Fetch candidates from contract
  useEffect(() => {
    const fetchCandidates = async () => {
      if (!electionId) return;
      
      try {
        setLoadingCandidates(true);
        
        // Get candidate IDs for this election
        const candidateIds = await getElectionCandidates(electionId);
        
        // Fetch details for each candidate
        const candidatePromises = candidateIds.map(async (candidateId) => {
          return await getCandidateDetails(candidateId);
        });
        
        const candidateDetails = await Promise.all(candidatePromises);
        setCandidates(candidateDetails.filter(candidate => candidate.isActive));
        
      } catch (error) {
        console.error('Error fetching candidates:', error);
        setCandidates([]);
      } finally {
        setLoadingCandidates(false);
      }
    };

    fetchCandidates();
  }, [electionId, getElectionCandidates, getCandidateDetails]);

  const handleVoteNow = (candidate) => {
    setSelectedCandidate(candidate);
  };

  const handleConfirmVote = async () => {
    if (!selectedCandidate || !account) return;
    
    try {
      setVotingInProgress(true);
      
      // For fingerprint verification, we'll use a placeholder hash
      // In a real implementation, this would come from biometric verification
      const fingerprintVerification = '0x' + '0'.repeat(64); // Placeholder hash
      
      await castVote(
        electionId,
        selectedCandidate.candidateId,
        fingerprintVerification
      );
      
      // Refresh candidates to get updated vote counts
      const candidateIds = await getElectionCandidates(electionId);
      const candidatePromises = candidateIds.map(async (candidateId) => {
        return await getCandidateDetails(candidateId);
      });
      const candidateDetails = await Promise.all(candidatePromises);
      setCandidates(candidateDetails.filter(candidate => candidate.isActive));
      
      setSelectedCandidate(null);
      alert('Vote cast successfully!');
      
    } catch (error) {
      console.error('Error casting vote:', error);
      alert('Error casting vote: ' + error.message);
    } finally {
      setVotingInProgress(false);
    }
  };

  const getTotalVotes = () => {
    return candidates.reduce((total, candidate) => total + parseInt(candidate.voteCount || 0), 0);
  };

  const getLeadingCandidate = () => {
    if (candidates.length === 0) return null;
    return candidates.reduce((leading, candidate) => 
      parseInt(candidate.voteCount || 0) > parseInt(leading.voteCount || 0) ? candidate : leading
    );
  };

  if (loadingCandidates) {
    return (
      <div className="space-y-6">
        <div className="bg-white rounded-lg shadow-sm p-12 text-center">
          <Loader2 className="w-16 h-16 text-blue-600 mx-auto mb-4 animate-spin" />
          <h3 className="text-xl font-semibold text-gray-600 mb-2">
            Loading Candidates...
          </h3>
          <p className="text-gray-500">
            Fetching candidate information from the blockchain.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Election Stats */}
      <div className="bg-white rounded-lg shadow-sm p-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="text-center">
            <div className="flex items-center justify-center w-12 h-12 bg-blue-100 rounded-lg mx-auto mb-2">
              <Users className="w-6 h-6 text-blue-600" />
            </div>
            <p className="text-2xl font-bold text-gray-900">{candidates.length}</p>
            <p className="text-sm text-gray-600">Candidates</p>
          </div>
          
          <div className="text-center">
            <div className="flex items-center justify-center w-12 h-12 bg-green-100 rounded-lg mx-auto mb-2">
              <BarChart3 className="w-6 h-6 text-green-600" />
            </div>
            <p className="text-2xl font-bold text-gray-900">{getTotalVotes()}</p>
            <p className="text-sm text-gray-600">Total Votes</p>
          </div>
          
          <div className="text-center">
            <div className="flex items-center justify-center w-12 h-12 bg-yellow-100 rounded-lg mx-auto mb-2">
              <Trophy className="w-6 h-6 text-yellow-600" />
            </div>
            <p className="text-lg font-bold text-gray-900 truncate">
              {getLeadingCandidate()?.name || 'TBD'}
            </p>
            <p className="text-sm text-gray-600">Leading</p>
          </div>
        </div>
      </div>

      {/* Results Toggle */}
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold text-gray-900">Candidates</h2>
        <button
          onClick={() => setShowResults(!showResults)}
          className="flex items-center gap-2 text-blue-600 hover:text-blue-700 font-medium transition-colors"
        >
          <BarChart3 className="w-4 h-4" />
          {showResults ? 'Hide Results' : 'Show Results'}
        </button>
      </div>

      {/* Candidates Grid */}
      {candidates.length === 0 ? (
        <div className="bg-white rounded-lg shadow-sm p-12 text-center">
          <Users className="w-16 h-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-xl font-semibold text-gray-600 mb-2">
            No Candidates Found
          </h3>
          <p className="text-gray-500">
            No candidates have been added to this election yet.
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {candidates.map((candidate) => (
            <CandidateCard
              key={candidate.candidateId}
              candidate={candidate}
              onVoteNow={handleVoteNow}
              canVote={isElectionActive}
              showResults={showResults}
              totalVotes={getTotalVotes()}
            />
          ))}
        </div>
      )}

      {/* Vote Confirmation Modal */}
      {selectedCandidate && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full p-6">
            <h3 className="text-xl font-bold text-gray-900 mb-4">
              Confirm Your Vote
            </h3>
            
            <div className="bg-gray-50 rounded-lg p-4 mb-6">
              <div className="flex items-center gap-4">
                <div className="w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white text-2xl font-bold">
                  {selectedCandidate.name.charAt(0)}
                </div>
                <div>
                  <h4 className="font-bold text-gray-900">{selectedCandidate.name}</h4>
                  <p className="text-gray-600">{selectedCandidate.partyName}</p>
                  <p className="text-2xl">{selectedCandidate.symbol}</p>
                </div>
              </div>
            </div>

            <p className="text-gray-600 mb-6">
              Are you sure you want to cast your vote for <strong>{selectedCandidate.name}</strong>? 
              This action cannot be undone.
            </p>

            <div className="flex gap-3">
              <button
                onClick={() => setSelectedCandidate(null)}
                disabled={votingInProgress}
                className="flex-1 bg-gray-200 hover:bg-gray-300 text-gray-800 font-medium py-2 px-4 rounded-lg transition-colors disabled:opacity-50"
              >
                Cancel
              </button>
              <button
                onClick={handleConfirmVote}
                disabled={votingInProgress}
                className="flex-1 bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition-colors disabled:opacity-50 flex items-center justify-center gap-2"
              >
                {votingInProgress ? (
                  <>
                    <Loader2 className="w-4 h-4 animate-spin" />
                    Voting...
                  </>
                ) : (
                  'Confirm Vote'
                )}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CandidateList;